<template>
  <Col span="20">
    <div class="content_right">
      <div class="content_title">{{title}}</div>
      <!-- 主体内容 -->
      <div class="content_main">
        <div class="tab_content" style="background-color:#fff">
          <Row>
            <Col span="19" push="5" style="background-color: #C9C9C9;">
              <div class="tools_top">
                <Row>
                  <Col span="8">
                    <label for="comment_content">通道名称:</label>
                    <Input type="text" id="comment_content" size="small"
                           v-model="comment_content"></Input>
                  </Col>
                  <Col span="3">
                    <div class="tools_search" @click="select()">
                      查询
                      <Icon type="search"></Icon>
                    </div>
                  </Col>
                  <Col span="3">
                    <div class="tools_search" @click="add">
                      添加
                      <Icon type="plus"></Icon>
                    </div>
                  </Col>
                </Row>
              </div>
              <Table class="tab" :columns="columns" :data="tabData" ellipsis border height="487"
                     highlight-row align="left"></Table>
            </Col>
            <Col span="5" pull="19">
              <div class="tools_top1">终端</div>
              <Tree :data="tree" @on-select-change="choiceAll" ref="tree4"></Tree>
            </Col>
          </Row>
        </div>
      </div>
    </div>
    <Modal v-model="modal1" width="400"
           @on-ok="ok"
           @on-cancel="cancel">
      <p slot="header" style="color:#2d8cf0;text-align:center">
        <span>电表信息</span>
      </p>
      <div style="text-align:center;">
        <Form ref="formInline" :model="meter" :label-width="80" inline>
          <FormItem label="电表名称">
            <Input v-model="meter.meterName" style="width:150px" :required="true"/>
          </FormItem>
          <FormItem label="电表号">
            <Input v-model="meter.meterAddr" style="width:150px"/>
          </FormItem>
          <FormItem label="状态">
            <Select v-model="meter.status" style="width:150px">
              <Option :value="1">已启用</Option>
              <Option :value="0">未启用</Option>
            </Select>
          </FormItem>
          <FormItem label="终端">
            <Select v-model="meter.ertuId" style="width:150px">
              <Option :value="ertu.id" v-for="(ertu,index) of tree" :key="index+1">{{ertu.title}}</Option>
            </Select>
          </FormItem>
          <FormItem label="规约">
            <Select v-model="meter.protocolId" style="width:150px">
              <Option :value="protocol.protocolId" v-for="(protocol,index) of protocols" :key="index+2">
                {{protocol.protocolName}}
              </Option>
            </Select>
          </FormItem>
          <FormItem label="端口名称">
            <Input v-model="meter.serialport" style="width:150px"/>
          </FormItem>
          <FormItem label="波特率">
            <Input v-model="meter.baudrate" style="width:150px"/>
          </FormItem>
          <FormItem label="数据位">
            <Input v-model="meter.dataBit" style="width:150px"/>
          </FormItem>
          <FormItem label="校验位">
            <Input v-model="meter.parityBit" style="width:150px"/>
          </FormItem>
          <FormItem label="停止位">
            <Input v-model="meter.stopBit" style="width:150px"/>
          </FormItem>
        </Form>
      </div>
    </Modal>
  </Col>
</template>
<script>
  export default {
    data() {
      return {
        tree: [],
        tabData: [],
        addErtuId:"",
        protocols: [],
        meter: {},
        loading: true,
        title: '',
        modal1: false,
        height_hide: {
          height: '0px',
          transform: '.5s all',
        },
        height_show: {
          height: 'auto',
          transform: '.5s all',
          overflow: 'hidden'
        },
        comment_content: '',
        columns: [
          {
            title: '序号',
            align: 'center',
            width: 60,
            type: 'index',
            ellipsis: true
          },
          {
            title: '电表名称',
            key: 'meterName',
            align: 'center',
            width: 100,
            ellipsis: true
          },
          {
            title: '电表号',
            key: 'meterAddr',
            width: 90,
            align: 'center',
            ellipsis: true
          },
          {
            title: '状态',
            key: 'status',
            width: 90,
            align: 'center',
            ellipsis: true,
            render: (h, params) => {
              var value;
                if (params.row.status===1) {
                  value = "已启用";
                }else {
                  value = "未启用";
                }
              return h('div', value)
            }
          },
          {
            title: '终端',
            key: 'ertuId',
            width: 80,
            align: 'center',
            ellipsis: true,
            render: (h, params) => {
              var value;
              this.tree.forEach(function (element) {
                if (element.id == params.row.ertuId) {
                  value = element.title;
                }
              });
              return h('div', value)
            }
          },
          {
            title: '规约ID',
            key: 'protocolId',
            width: 90,
            align: 'center',
            ellipsis: true
          },

          {
            title: '端口名称',
            key: 'serialport',
            width: 90,
            align: 'center',
            ellipsis: true
          },
          {
            title: '波特率',
            key: 'baudrate',
            width: 80,
            align: 'center',
            ellipsis: true
          },
          {
            title: '数据位',
            key: 'dataBit',
            width: 80,
            align: 'center',
            ellipsis: true
          },
          {
            title: '校验位',
            key: 'parityBit',
            width: 80,
            align: 'center',
            ellipsis: true
          },
          {
            title: '停止位',
            key: 'stopBit',
            width: 80,
            align: 'center',
            ellipsis: true
          },

          {
            title: '操作',
            key: 'action',
            width: 150,
            align: 'center',
            fixed: 'right',
            render: (h, params) => {
              return h('div', [
                h('Button', {
                  props: {
                    type: 'primary',
                    size: 'small'
                  },
                  style: {
                    marginRight: '5px'
                  },
                  on: {
                    click: () => {
                      this.loadTree();
                      this.loadProtocols();
                      this.modal1 = true;
                      this.meter = params.row
                    }
                  }
                }, '修改'),
                h('Button', {
                  props: {
                    type: 'error',
                    size: 'small'
                  },
                  on: {
                    click: () => {
                      this.delete(params.row);
                    }
                  }
                }, '删除')
              ]);
            }
          }
        ],
      }
    },
    methods: {
      renderContent(h, {root, node, data}) {
        return h('span', {
          style: {
            display: 'inline-block',
            width: '100%'
          }
        }, [
          h('span', [
            h('Icon', {
              props: {
                type: 'ios-paper-outline'
              },
              style: {
                marginRight: '8px'
              }
            }),
            h('span', data.title)
          ])
        ]);
      },
      loadTree() {
        this.$http(`/ertu/ertuTree`)
          .then(res => {
            if (res.data.status === 'success') {
              this.tree = res.data.results.children;
            }
          });
      },
      loadData(ertuId) {
        var url;
        if(this.addErtuId===''){
          url = "/meter/findAll"
        }else {
          url = `/meter/findMeterByErtuId?ertuId=${this.addErtuId}`
        }
        this.$http(url)
          .then(res => {
            if (res.data.status == 'success') {
              this.tabData = res.data.results;
            }
          })
      },
      loadProtocols() {
        this.$http(`/protocol/findAll`)
          .then(res => {
            if (res.data.status == 'success') {
              this.protocols = res.data.results;
            }
          })
      },
      add() {
        this.ertu = {};
        this.loadTree();
        this.loadProtocols();
        this.modal1 = true;
      },
      ok() {
        this.$http.post('/meter/save', this.meter)
          .then(res => {
            if (res.data.status === 'success') {
              this.$Message.info('操作成功！');
            } else {
              this.$Message.info('操作失败！');
            }
            this.loadData();
          })
      },
      cancel() {
        this.$Message.info('您取消了添加操作！');
        this.loadData();
      },
      select() {
        if (this.addErtuId === '') {
          this.$Message.info("请在左侧选择终端！");
        }
        else if (this.comment_content == null || this.comment_content === '') {
          this.$Message.info("请输入查询内容！");
          this.loadData();
        }
        else {
          this.$http(`/meter/findSimilar?meterName=${this.comment_content}&ertuId=${this.addErtuId}`)
            .then(res => {
              if (res.data.status === 'success' && res.data.results !== '') {
                this.tabData = res.data.results;
              }
              else {
                this.$Message.info('未查询找到相关信息！');
              }
            });
        }
      },
      delete(row) {
        this.$Modal.confirm({
          title: "确认删除该条目",
          content: "执行删除操作之后，可能影响系统功能，请谨慎操作！",
          onOk: () => {
            this.$http(`/meter/delete?id=${row.meterId}`)
              .then(res => {
                if (res.data.status == 'success') {
                  this.loadData();
                  this.$Message.info('操作成功！');
                } else {
                  this.$Message.info('操作失败！');
                }
              })
          },
          onCancel: () => {
            this.$Message.info('您取消了添加操作！');
          }
        });
      },
      choiceAll: function (data) {
        if (data[0] == null) {
          this.tabData = [];
          this.addErtuId = '';
        } else {
          this.addErtuId = data[0].id;
          this.loadData(this.addErtuId)
        }
      }
    },
    created: function () {
      this.loadTree();
      this.loadData();
      this.loadProtocols();
      if (this.$route.query.title == null) {
        this.title = '电表管理';
      } else {
        this.title = this.$route.query.title;
      }
    },
  }
</script>

<style scoped>
  body {
    font-family: "Source Sans Pro", sans-serif;
  }

  .content_left {
    display: inline-block;
    width: 100%;
    height: 100%;
    background: radial-gradient(ellipse at top, #30343D 20%, #323E50 60%, #3A5274 100%);
  }

  .content_right {
    display: inline-block;
    background-color: #D6DBDF;
    width: 100%;
    height: 100%;
  }

  .content_title {
    width: 100%;
    height: 30px;
    line-height: 30px;
    text-align: center;
    background-color: #005C7E;
    color: #fff;
  }

  .ivu-row {
    height: 100%;
    margin: 0;
    padding: 0;
  }

  .ivu-col {
    height: 100%;
    margin: 0;
    padding: 0;
  }

  .menu_box {
    margin-top: 15px;
    color: #C8CCD7;
  }

  .menu_btn {
    width: 100%;
    height: 50px;
    line-height: 50px;
    background-color: #373B47;
    border-top: 1px solid #333742;
    border-bottom: 1px solid #333742;
    text-align: center;
    position: relative;
    cursor: pointer;
    user-select: none;
  }

  .btn_active {
    background-color: #20232C !important;
  }

  .angle_active::after {
    content: '';
    display: inline-block;
    width: 0px;
    height: 0px;
    border: 8px solid transparent;
    border-right: 8px solid #fff;
    position: absolute;
    right: 0px;
    top: 50%;
    margin-top: -8px;
  }

  .btn_show::after {
    content: '';
    display: inline-block;
    width: 0px;
    height: 0px;
    border: 8px solid transparent;
    position: absolute;
    right: 0px;
    top: 50%;
    border-top: 8px solid #fff !important;
    margin-top: -4px !important;
  }

  .angle_active::before {
    content: '';
    display: inline-block;
    width: 0px;
    height: 100%;
    border-right: 3px solid #81B1EB !important;
    position: absolute;
    left: 3px;
  }

  .angle::before {
    content: '';
    display: inline-block;
    width: 0px;
    height: 100%;
    border-right: 3px solid #536A86;
    position: absolute;
    left: 3px;
  }

  .tools_top {
    height: 30px;
    vertical-align: middle;
    padding-top: 5px;
    background: #DBEFFA;
    padding-left: 10px;
    margin-left: 1px;
  }

  .tools_top1 {
    height: 30px;
    vertical-align: middle;
    padding-top: 5px;
    background: #DBEFFA;
    padding-left: 10px;
  }

  .tools_search {
    display: inline-block;
    width: 60px;
    height: 22px;
    line-height: 22px;
    background-color: #2d8cf0;
    color: #fff;
    font-size: 12px;
    text-align: center;
    cursor: pointer;
  }

  .tools_search > i {
    font-size: 12px;
  }

  .tools_icon {
    display: inline-block;
    width: 20px;
    height: 20px;
    margin: 0 5px;
    color: #2A8BBC;
    line-height: 20px;
    font-size: 20px;
    cursor: pointer;
  }

  .ivu-input-wrapper {
    width: 60%;
  }

  .btn_col {
    margin-top: 15px;
  }

  .main_btn {
    display: inline-block;
    width: 100px;
    border: 1px solid #000;
    height: 30px;
    margin: 0 10px;
    background-color: #eee;
    text-align: center;
    border-radius: 5px;
    vertical-align: middle;
    line-height: 30px;
  }
</style>



